import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomePage.vue";
import AboutView from "../views/AboutPage.vue";
import ListingView from "../views/ListingPage.vue";
import DetailsView from "../views/DetailsPage.vue";
import ContactView from "../views/ContactPage.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/about",
    name: "about",
    component: AboutView,
  },
  {
    path: "/listings",
    name: "listings",
    component: ListingView,
  },
  {
    path: "/view/details/podcastId=:Id",
    name: "details",
    component: DetailsView,
  },
  {
    path: "/contact",
    name: "contact",
    component: ContactView,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  document.title = `${process.env.VUE_APP_TITLe} ${to.name}`;
  window.scrollTo({
    left: 0,
    top: 0,
    behavior: "instant",
  });
  next();
});

export default router;
