<template>
  <div class="loader" v-if="isloaded">
    <div class="loader-inner">
      <div class="circle"></div>
    </div>
  </div>
  <!--PreLoader Ends-->
</template>

<script>
export default {
  data: () => {
    return {
      isloaded: true,
    };
  },
  mounted() {
    document.onreadystatechange = () => {
      if (document.readyState === "complete") {
        this.isloaded = false;
      }
    };
  },
};
</script>

