<template>
  <div class="details" v-for="podCast in podCast" :key="podCast.id">
    <DetailsVue :podCast="podCast" />
  </div>
</template>
<script>
import DetailsVue from "@/components/Details/DetailsVue.vue";
import getPodcast from "@/json/podCasts.json";
import { useRoute } from "vue-router";
import { ref, onBeforeMount } from "vue";
export default {
  name: "details-page",
  setup() {
    const route = useRoute();
    const podCast = ref([]);
    const GetPod = getPodcast;
    const podCastId = route.params.Id;
    onBeforeMount(() => {
      let data = [];
      for (let i = 0; i < GetPod.length; i++) {
        if (GetPod[i].id == podCastId) {
          data.push(GetPod[i]);
          podCast.value = data;
        }
      }
    });
    return {
      podCast,
    };
  },
  components: {
    DetailsVue,
  },
};
</script>
